import React from 'react';
import PropTypes from 'prop-types';
import { Form, Control, Errors } from 'react-redux-form';

const Login = ({ onCheckCredentials, loginSuccess }) => (
  <div className="content">
    <Form className="loginForm">
      <Control.text
        model="user.username"
        validators={{
          required: val => val && val.length,
        }}
        validateOn="blur"
        ref={(input) => { this.username = input; }}
        placeholder="username"
      />
      <Control.text
        model="user.password"
        validators={{
          required: val => val && val.length,
        }}
        validateOn="blur"
        ref={(input) => { this.password = input; }}
        placeholder="password"
      />
      <input
        type="button"
        className="loginBtn"
        onClick={
          () => onCheckCredentials({ username: this.username.value, password: this.password.value })
        }
        value="login"
      />
      {
        !loginSuccess && loginSuccess !== null ?
          <span className="message">Please fill valid Username/Password</span>
          : null
      }
      <Errors messages={{ required: 'Required' }} />
    </Form>
  </div>
);

Login.propTypes = {
  loginSuccess: PropTypes.bool.isRequired,
  onCheckCredentials: PropTypes.func.isRequired,
};

export default Login;




